/* Header */

header {
    background-color: #039be5;
    color: #fafafa;
    font-family: 'Francois One', sans-serif;
    padding: 40px 0;
}

@media (min-width: 500px) {
    .header {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
    }
}

.header-title {
    -ms-flex: 1 1;
        flex: 1 1;
}

.header-title > h1 {
    margin-top: 0;
    font-size: 3.4em;
}

.header-title > span {
    font-size: 2em;
}

.header-links {
    justify-self: flex-end;
}

.header-icon {
    font-size: 3em;
    margin: 15px;
    color: #fafafa;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.header-icon:hover {
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
}

/* Sections */

.section {
    margin-top: 25px;
}

.section > h2 {
    margin: 40px 0;
    text-align: center;
    font-size: 2.4em;
    text-transform: uppercase;
    color: #f4511e;
    font-weight: bold;
    letter-spacing: 2px;
    font-family: 'Francois One', sans-serif;
}

hr {
    margin: 40px 0;
}

/* Projects */

.project {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin: 40px 0;
}

.project > img {
    display: none;
}

@media (min-width: 768px) {
    .project > img {
        display: block;
        width: 150px;
        height: auto;
    }

    .project > .info {
        margin-left: 50px;
    }
}

.project > .info > h3 {
    font-family: 'Francois One', sans-serif;
    font-size: 2em;
    margin-top: 0;
}

.project > .info > p {
    font-size: 1.5em;
}

.project > .info > a {
    font-family: 'Francois One', sans-serif;
    font-size: 1.5em;
    text-decoration: none;
    -webkit-transition: 0.5s all;
    -o-transition: 0.5s all;
    transition: 0.5s all;
    color: #039be5;
    letter-spacing: 1px;
    display: block;
    margin-bottom: 5px;
}

.project > .info > a:hover {
    color: #027db8;
}

/* Expertise */


.expertise {
    margin-top: 20px;
}

@media (min-width: 768px) {
    .expertise {
        display: -ms-flexbox;
        display: flex;
    }
}

.expertise:first-of-type {
    margin-top: 40px;
}

.expertise-title {
    font-family: 'Francois One', sans-serif;
    font-size: 1.7em;
    -ms-flex: 0 0 200px;
        flex: 0 0 200px;
}

.expertise-description {
    font-size: 1.5em;
}

/* More information */

.more {
    text-align: center;
    font-size: 1.5em;
}

/* Footer */

footer {
    background-color: #039be5;
    color: #fafafa;
    text-align: center;
    margin-top: 25px;
    padding: 25px 0;
    font-size: 1.3em;
}


